.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background: #fff;
    box-shadow: 0px 2px 5px rgba(0,0,0,0.2);
    border-radius: 5px;
    margin-top: 30px;
}
h1 {
    font-size: 36px;
    text-align: center;
    margin-bottom: 30px;
}
.input-container {
    display: flex;
    gap: 10px;
    margin-bottom: 30px;
}
input {
    flex: 1;
    padding: 10px;
    font-size: 18px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.add-btn {
    border-radius: 5px;
    padding: 10px;
    font-size: 18px;
    color: #fff;
    background-color: #007bff;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}
.reminder-list {
    list-style: none;
    margin: 0;
    padding: 0;
}
.reminder-list li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f7f7f7;
    border: 1px solid #ccc;
    padding: 10px;
    margin-bottom: 10px;
}
.delete-btn {
    padding: 5px 10px;
    font-size: 16px;
    color: #fff;
    background-color: #dc3545;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}